.ownedNfts {
    height: 100vh;
    background-color: inherit;
    display: flow-root;
    overflow: auto;

    .title {
        width: 100%;
        display: flex;
        justify-content: center;
    }

    .tabs {
        display: flex;
        justify-content: center;
        margin-top: 30px;

        .active {
            text-decoration: underline;
            color: wheat;
        }

        .tab {
            cursor: pointer;
            font-size: x-large;
        }



        :first-child {
            margin-right: 15px;
        }
    }


    .wrapper {

        margin-top: 50px;
        width: 100%;
        display: flex;
        justify-content: flex-start;
        flex-direction: row;
        padding-bottom: 50px;

        .content-list {
            display: grid;
            grid-template-columns: repeat(4,1fr);
            row-gap: 30px;
            width: 100%;
            place-items: center;

            .content {
                display: flex;
                justify-content: center;
                background-color: #2D4263;
                border-radius: 10%;
                box-shadow: 1px 1px 14px 3px #000000;
                width: 300px;

                .item {

                    .nft {
                        padding: 10px 10px 10px 10px;
                        background-color: lightgray;

                        .nft-name {
                            text-align: center;
                            font-size: 30px;
                            color: #191919;
                            font-weight: 450;
                            padding-bottom: 10px;
                            padding-top: 10px;

                        }

                        .nft-image {

                            .image {
                                width: 200px;
                                height: 200px;
                                border: 2px solid rgba(0, 0, 0, 0.686);
                            }
                        }

                        .nft-actions {
                            display: flex;
                            justify-content: space-between;
                            margin-top: 15px;
                            font-size: 14px;

                            .edit-price{
                                display: flex;
                                flex-direction: column;
                                width: 50%;
                                gap: 10px;

                                .edit-icon{
                                    margin-left: 5px;
                                }
                            }

                            button{
                                background-color: #7393B3;
                                border-radius: 10px;
                                transition: all 1s ease;
                                width: fit-content;
                                font-size: 1.25em;
                              }
        

                        }
                    }


                }
            }
        }

    }
}

button:hover{
    background-color: #234fec !important;
    cursor: pointer;
  }